.animal-details-top {
  margin-top: 1em;
  margin-bottom: 1em;
}
.animal-details-top .edit-button {
  padding-left: 0;
  padding-right: 0.25em;
}
.animal-details-top form {
  display: inline-block;
}
.animal-details-top form button {
  padding-left: 0.25em;
}

.tab-content .number-column {
  width: 15%;
}
.tab-content #vet-visits {
  margin-left: 1rem;
}
.tab-content #files, .tab-content #images {
  margin-left: 1rem;
}
.tab-content .animal-images {
  width: 100%;
}
.tab-content .animal-images .animal-image {
  margin-right: 1em;
  margin-bottom: 1em;
  float: left;
}
.tab-content .animal-images .animal-image a {
  height: 250px;
}

.card-img {
  object-fit: contain;
  height: 200px;
  width: 200px;
}

.animal-badges {
  width: 150px;
}
.animal-badges .badge {
  width: 150px;
}